.body {
	background-color: #dfe6e9;
}

.extendedCanvas {
	border-radius: 20px;
	position: absolute;
	top: 20;
	bottom: 20;
	left: 0;
	right: 0;
	margin: auto;
}

.center {
	display: flex;
	justify-content: center;
	align-items: center;
}

/* Hide the original file upload so we can show the new design */
input[type="file"] {
	display: none;
}
.customFileUpload {
	background-color: #008f73;
	color: #dfe6e9;
	border-radius: 5px;
	display: inline-block;
	padding: 6px 12px;
	cursor: pointer;
}

.button {
	background-color: #008f73;
	border: none;
	color: #dfe6e9;
	border-radius: 5px;
	display: inline-block;
	text-align: center;
	text-decoration: none;
	cursor: pointer;
	padding: 6px 12px;
}
.button-disabled {
	background-color: #5eb1a1;
	border: none;
	color: #dfe6e9;
	border-radius: 5px;
	display: inline-block;
	text-align: center;
	text-decoration: none;
	cursor: pointer;
	padding: 6px 12px;
}

.small {
	/*font-size: 15px;*/
}

a:link {
	color: #636e72;
	background-color: transparent;
	text-decoration: underline;
}
a:visited {
	color: #636e72;
	background-color: transparent;
	text-decoration: underline;
}

a:active {
	color: #636e72;
	background-color: transparent;
	text-decoration: underline;
}
